import React, { Component } from 'react'
import './App.css';
import shuffle from 'lodash.shuffle'

const CARDS = 'ðŸš‚ðŸšƒðŸš¡ðŸ›°ï¸ðŸš€ðŸ›¸ðŸš•ðŸš–ðŸš—ðŸš˜ðŸš›ðŸšœðŸš²ðŸ›´ðŸ›µðŸšðŸšŽðŸšðŸš‘ðŸš’âœˆï¸'
const HIDDEN_SYMBOL = 'â“'
const SIZE = 42


const Card = ({ card, feedback, index, onClick }) => (
  <div className={`card ${feedback}`} onClick={() => onClick(card, index)}>
    <span className="symbol">
      {feedback === 'hidden' ? HIDDEN_SYMBOL : card}
    </span>
  </div>
)
const GuessCount = ({ guesses }) => ((guesses > 1) ? <div className="guesses"> {guesses} guesses</div> : <div className="guesses"> {guesses} guess</div>)


class App extends Component {
  state = {
    board: this.generateGameBoard(),
    currentPair: [],
    guesses: 0,
    stateOfCards: this.generateSateOfCards(),
    matchedCardIndices: [],
  }

  generateSateOfCards() {
    let result = [];
    while (result.length < SIZE) {
      result.push("hidden")
    }
    return result
  }

  generateGameBoard() {
    const result = []
    const candidates = shuffle(CARDS)
    while (result.length < SIZE) {
      const card = candidates.pop()
      result.push(card, card)
    }
    return shuffle(result)
  }

  // Arrow fx for binding
  handleCardClick = (card, index) => {
    let current = this.state.currentPair;
    let matchedCard = this.state.matchedCardIndices;
    let state = this.state.stateOfCards;
    if (state[index] !== "hidden") { return }
    if (this.state.currentPair.length === 4) {
      if (state[current[1]] !== "justMatched") {
        state[current[1]] = "hidden";
        state[current[3]] = "hidden";
      }
      this.setState({
        currentPair: [],
        stateOfCards: state,
      });
      return;
    }
    state[index] = "visible";
    this.setState({ stateOfCards: state })
    if (current.length < 4) {
      current.push(card, index);
      this.setState({ currentPair: current });
    }
    if (this.state.currentPair.length === 4) {
      if (this.state.currentPair[0] === this.state.currentPair[2]) {
        matchedCard.push(current[1], current[3]);
        state[current[1]] = "justMatched";
        state[current[3]] = "justMatched";
        this.setState({ matchedCardIndices: matchedCard })
      }
      else {
        state[current[1]] = "justMismatched";
        state[current[3]] = "justMismatched";
      }
      this.setState({ stateOfCards: state });
      this.setState({ guesses: this.state.guesses + 1 });
    }
    console.log(card, this.state)
  }

  render() {
    const { board, stateOfCards, guesses, matchedCardIndices } = this.state
    const won = matchedCardIndices.length === board.length
    const wonMessage = "you won in " + guesses + " tries"
    return (
      <div className="memory">
        <GuessCount guesses={guesses} />
        {board.map((card, index) => (
          <Card
            card={card}
            feedback={stateOfCards[index]}
            index={index}
            key={index}
            onClick={this.handleCardClick}
          />
        ))}
        {won && wonMessage}
      </div>
    )
  }
}

export default App;
